import { Component, EventEmitter, Input, Output } from '@angular/core';
import { FormControl, Validators } from '@angular/forms';
import { NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { DepositService } from '../../../../shared/services/deposit.service';
import { MessageModalComponent } from '../../../../shared/modals/message-modal/message-modal.component';
import { DatePipe } from '@angular/common';
import { ChequeActionEnum, ChequeStatusEnum } from '../../../../shared/enums/enum';
import { Observable } from 'rxjs';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-confirm-action',
  templateUrl: './confirm-action.component.html',
  styleUrls: ['./confirm-action.component.scss']
})
export class ConfirmActionComponent {
  @Input() data;
  details;
  chequeStatusId = 0;
  @Output() eventData = new EventEmitter();
  bankConfirmationDate = new FormControl(new Date());
  rejectReason = new FormControl('');
  chequeStatusEnum = ChequeStatusEnum;
  chequeActionEnum = ChequeActionEnum;
  action: 'confirm' | 'reject';
  constructor(
    public _activeModal: NgbActiveModal,
    public depositservice: DepositService,
    public _modalService: NgbModal,
    public _datePipe: DatePipe,
    public _spinner: NgxSpinnerService,
  ) {
  }
  ngOnInit() {
    this.details = this.data;
    this.action = this.details.cusAction;
    this.chequeStatusId = this.details?.status;
    if (this.action == 'confirm') {
      this.bankConfirmationDate.setValidators([Validators.required]);
      this.bankConfirmationDate.updateValueAndValidity();
    }
    else if (this.action == 'reject') {
      this.rejectReason.setValidators([Validators.required]);
      this.rejectReason.updateValueAndValidity();

    }
  }
  confirm() {

    let URL: Observable<any>;
    let formData = new FormData();
    formData.append('depositedChequeId', this.details?.id);
    if (this.action == 'confirm') {
      if (this.chequeStatusId == ChequeStatusEnum.PendingPostpone
        || this.chequeStatusId == ChequeStatusEnum.PendingReplace
        || this.chequeStatusId == ChequeStatusEnum.PendingWithdraw
        || this.chequeStatusId == ChequeStatusEnum.PendingReDeposit
      ) {
        formData.append('BankConfirmationDate', this._datePipe.transform(this.bankConfirmationDate.value, 'yyyy-MM-dd') || '');
      }
      URL = this.depositservice.confirmChequeAction(formData)
    }
    else if (this.action == 'reject') {
      formData.append('rejectReason', this.rejectReason.value);
      URL = this.depositservice.rejectChequeAction(formData);
    }
    this._spinner.show();
    URL.subscribe({
      next: response => {
        if (response.isSuccess) {
          this.responseModal('success', 'Action successfully done.');
          this.eventData.emit({ success: true });
          this._activeModal.close();
        }
      },
      error: (err: any) => {
        this.responseModal('error', err?.error?.errors[0].errorMessageEn || err?.error?.errors[0].ErrorMessageEn || err?.info);
      }
    }).add(() => this._spinner.hide())
  }
  responseModal(type: any, message: any) {
    const ref = this._modalService.open(MessageModalComponent);
    ref.componentInstance.type = type;
    ref.componentInstance.message = message;
  }
}
