import { Component, EventEmitter, Input, Output } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { ChequeActionEnum, ChequeStatusEnum } from '../../shared/enums/enum';

@Component({
  selector: 'app-deposit-cheque-detail-modal',
  templateUrl: './deposit-cheque-detail-modal.component.html',
  styleUrls: ['./deposit-cheque-detail-modal.component.scss']
})
export class DepositChequeDetailModalComponent {
  @Input() details: any;
  @Input() title = '';
  @Input() detailShow = false;
  @Output() sendtoLoadData = new EventEmitter();
  chequeStatusEnum = ChequeStatusEnum;
  chequeActionEnum = ChequeActionEnum;
  statusId = 0;
  actionId = 0;
  constructor(private modalService: NgbModal
  ) { }
  ngOnInit() {
    this.statusId = this.details?.statusObj?.lookupId;
    this.actionId = this.details.chqActionId;
  }
  dismissModal() {
    this.modalService.dismissAll();
  }
  submit() {
    this.sendtoLoadData.emit();
  }
}
