import { Component, ViewChild } from '@angular/core';
import DatalabelsPlugin from 'chartjs-plugin-datalabels';
import { ChartConfiguration, ChartData, ChartEvent, ChartType } from 'chart.js';
import { BaseChartDirective } from 'ng2-charts';
import { DepositService } from '../../../shared/services/deposit.service';
import { DatePipe } from '@angular/common';
import { truncate } from 'lodash';
import { last } from 'rxjs';

@Component({
  selector: 'cheques-due-date',
  templateUrl: './cheques-due-date.component.html',
  styleUrls: ['./cheques-due-date.component.scss'],
  providers: [DatePipe],
})
export class ChequesDueDateComponent {
  dueChequeList: any[] = [];
  bsRangeValue: Date[] = [];
  pieChartData: any;
  @ViewChild(BaseChartDirective) chart: BaseChartDirective | undefined;

  constructor(private depositservice: DepositService, private _datePipe: DatePipe) {
    const currentDate = new Date();
    let firstDay = new Date(currentDate.getFullYear(), currentDate.getMonth(), 1);
    let lastDay = new Date(currentDate.getFullYear(), currentDate.getMonth() + 1, 0);
    this.bsRangeValue = [firstDay, lastDay];

  }
  ngOnInit() {
    this.getDueCheques();
  }
  getDueCheques(from?: any, to?: any) {
    const currentDate = new Date();
    let firstDay = new Date(currentDate.getFullYear(), currentDate.getMonth(), 1);
    let lastDay = new Date(currentDate.getFullYear(), currentDate.getMonth() + 1, 0);

    let fromDate = this._datePipe.transform(from || firstDay, 'yyyy-MM-dd');
    let toDate = this._datePipe.transform(to || lastDay, 'yyyy-MM-dd');
    let param = `?FromDate=${fromDate}&ToDate=${toDate}`;
    this.depositservice.getDueCheques(param).subscribe(res => {
      this.dueChequeList = res.data;
      let amount = this.dueChequeList.map(x => x.totalAmount);
      let bank = this.dueChequeList.map(x => x.bank);
      this.pieChartData = {
        labels: bank,
        datasets: [
          {
            data: amount,
            backgroundColor: ['#253951', '#DC3545', '#C4CDD5', '#767271', '#B5B5B4'],
          }
        ],
      }
    })
  }
  onDateValueChange(event: any) {
    var pipe = new DatePipe('en-US');
    let startDate = pipe.transform(event[0]) || '';
    let endDate = pipe.transform(event[1]) || '';
    this.getDueCheques(startDate, endDate);
  }
  // Pie
  public pieChartOptions: ChartConfiguration['options'] = {
    responsive: true,
    maintainAspectRatio: false,
    plugins: {
      legend: {
        display: true,
        position: 'top',
      },
      datalabels: {
        color: 'white',
        formatter: (value: any, ctx: any) => {
          if (ctx.chart.data.labels) {
            return ctx.chart.data.labels[ctx.dataIndex];
          }
        },
      },
    },
  };
  public pieChartType: ChartType = 'pie';
  public pieChartPlugins = [DatalabelsPlugin];

  // events
  public chartClicked({ event, active, }: { event: ChartEvent; active: any; }): void {
    console.log(event.native?.currentTarget)
  }

  public chartHovered({ event, active, }: {
    event: ChartEvent;
    active: object[];
  }): void {
    console.log(event, active);
  }
}
