import { Component, OnDestroy, OnInit } from '@angular/core';
import { Subject, debounceTime, distinctUntilChanged, forkJoin, takeUntil } from 'rxjs';
import { TableColumn, TableConfig } from 'src/app/shared/components/table-advanced';
import { DepositService } from 'src/app/shared/services/deposit.service';
import { DatePipe } from '@angular/common';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { DepositChequeModalComponent } from '../deposit-cheque-modal/deposit-cheque-modal.component';
import { ReplaceChequeComponent } from '../replace-cheque/replace-cheque.component';
import { ReplacedChequeDetailViewComponent } from '../cheque-dashboard/replaced-cheque-detail-view/replaced-cheque-detail-view.component';
import { FormControl } from '@angular/forms';
import { PermissionService } from 'src/app/shared/services/permission.service';
import { PostponeChequeComponent } from '../postpone-cheque/postpone-cheque.component';
import { AuthService } from '../../shared/services/auth.service';
import { Router } from '@angular/router';
import { ChequeActionEnum, ChequeStatusEnum } from '../../shared/enums/enum';
import { CreditCardService } from '../../shared/services/credit-card.service';
import { ExportToExcelOptions } from '../../shared/Interfaces/IExcel';
import { HelperService } from '../../shared/services/helper.service';
import { ExcelService } from '../../shared/services/excel.service';

@Component({
  selector: 'app-cheque-list',
  templateUrl: './cheque-list.component.html',
  styleUrls: ['./cheque-list.component.scss'],
  providers: [DatePipe],
})
export class ChequeListComponent implements OnInit, OnDestroy {
  searchText = new FormControl(null);
  searchChequeText = new FormControl(null);
  statusIdControl = new FormControl(null);
  chequeType = new FormControl('');
  loading = false;
  page = 1;
  total = 0;
  limit = 6;
  sort: number = 1;
  tableConfig: TableConfig = {
    paging: true,
    filter: {
      Sort: 1,
      PageSize: this.limit,
    },
  };
  search = new FormControl(null);
  tableColumns: TableColumn[] = [];
  deposites: any = [];
  totalAllRecordsCount = 0;
  unsubscribe = new Subject<void>();
  statusId: any;
  actionList: any[] = [];
  markaziaModel = [];
  showMultiSelect = false;
  userDetails: any;
  pendingChequesActions = [28008, 28009, 28010, 28011];
  chequeStatusEnum = ChequeStatusEnum;
  chequeActionEnum = ChequeActionEnum;

  constructor(
    private modalService: NgbModal, private depositservice: DepositService,
    private permission: PermissionService,
    private _datePipe: DatePipe,
    private _authService: AuthService,
    private creditcardservice: CreditCardService,
    private _router: Router,
    private _helperService: HelperService,
    private _excelService: ExcelService,
  ) { }

  ngOnInit(): void {
    let values = JSON.parse(localStorage.getItem('chequeFilters')!);
    if (values?.searchChequeText) {
      this.searchChequeText.setValue(values?.searchChequeText);
      this.tableConfig.filter.ChequeNo = values?.searchChequeText;
    }
    if (values?.searchText) {
      this.searchText.setValue(values?.searchText);
      this.tableConfig.filter.Customer = values?.searchText;
    }
    if (values?.statusIdControl) {
      this.statusIdControl.setValue(values?.statusIdControl);
      this.tableConfig.filter.Status = values?.statusIdControl;
    }
    if (values?.chequeType) {
      this.chequeType.setValue(values?.chequeType);
      this.tableConfig.filter.IsPDC = values?.chequeType;
    }
    localStorage.removeItem('chequeFilters');
    this.getbanks();
    this.initTableColumns();
    this.fetchData();
    this.getUserDetails();
    this.searchChequeText.valueChanges
      .pipe(debounceTime(300), distinctUntilChanged(), takeUntil(this.unsubscribe))
      .subscribe(value => {
        const text: string = value || '';
        if (text?.length >= 3 || (!text?.length && this.tableConfig.filter.ChequeNo?.length)) {
          this.tableConfig.filter.ChequeNo = text;
          this.page = 1;
          this.selecteddata = [];
          this.fetchData();
        }
      });
    this.searchText.valueChanges
      .pipe(debounceTime(300), distinctUntilChanged(), takeUntil(this.unsubscribe))
      .subscribe(value => {
        const text: string = value || '';
        if (text?.length >= 3 || (!text?.length && this.tableConfig.filter.Customer?.length)) {
          this.tableConfig.filter.Customer = text;
          this.selecteddata = [];
          this.page = 1;
          this.fetchData();
        }
      });
  }
  getChequesForExport() {
    this.loading = true;
    this.tableConfig.filter.PageNo = this.page - 1;
    this.tableConfig.filter.PageSize = 10000;
    this.depositservice
      .getDepositCheques(this.tableConfig.filter)
      .subscribe(result => {
        if (result) {
          this.tableConfig.filter.PageSize = this.limit;
          this.generateExcelExport(result.data);
        }
      }).add(() => (this.loading = false));
  }
  async generateExcelExport(data) {
    data = data.map(x => {
      return {
        ...x,
        chequeDate: this._helperService.dateFormate(x?.chequeDate),
        collectedAt: this._helperService.dateFormate(x?.collectedAt),
        markaziaBA: `${x?.account?.bankNameEnglish}-${x?.account?.bankNameArabic}-${x?.account?.accountNumber}`
      }
    })
    const exportOptions: ExportToExcelOptions = {
      fileName: 'Cheques_List',
      mainHeading: '',
      sections: [
        {
          heading: [],
          headerStyle: {
            bold: true, size: 12, color: { argb: 'ff000000' }, fgColor: { argb: 'ffe7e6e6' },
          },
          headers: ['Cheque No', 'Customer Account', 'Customer Name', 'Cheque Bank', 'Due Date',
            'Amount', 'Currency', 'Amount (JOD)', 'Collection Date', 'Markazia Bank Account', 'Status',
          ],
          mainKeyMapping: ['chequeNo', 'customer.customerId', 'customer.customerName', 'bank', 'chequeDate',
            'collectedAmount', 'currency[0].lookupName', 'chequeCollectionJod', 'collectedAt', 'markaziaBA', 'statusObj.translations[0].lookupName',
          ],
          data: data,
        },
      ]
    };
    const result = await this._excelService.exportToExcelJs1(exportOptions);
  }

  getUserDetails() {
    this._authService.getLoggedUserDetails().subscribe(response => {
      this.userDetails = response?.data;
    })
  }
  ngOnDestroy() {
    this.unsubscribe.next();
    this.unsubscribe.complete();
    let nextRoute = (this._router.url);
    if (nextRoute.includes('/deposit-cheque/detail')) {
      let filters = {
        searchChequeText: this.searchChequeText.value,
        searchText: this.searchText.value,
        statusIdControl: this.statusIdControl.value,
        chequeType: this.chequeType.value,
      }

      localStorage.setItem('chequeFilters', JSON.stringify(filters))
    }
  }

  fetchData() {
    this.loading = true;
    // this.makeDepositList(depositChequeList);
    // this.tableConfig.filter.BranchTypeId = this.selectedBranch.id;
    this.tableConfig.filter.PageNo = this.page - 1;
    this.depositservice.getDepositCheques(this.tableConfig.filter).subscribe(result => {
      if (result) {
        this.makeListData(result.data || []);
        if (this.selecteddata.length > 0) {
          this.deposites.forEach((element: any) => {
            let findElement = this.selecteddata.find(elem => elem.id == element.id);
            if (findElement)
              element.checked = true;
          });
        }
        this.totalAllRecordsCount = result?.totalRecordCount;
        this.total = result?.totalRecordCount;
      }
    })
      .add(() => (this.loading = false));
  }
  makeListData(data: any) {
    const updatedData = data.map((item: any) => {
      let chequeType = item.statusObj?.translations?.[0]?.lookupName;
      let dueDate = this._datePipe.transform(item.chequeDate, 'yyyy-MM-dd');
      const isCollectedInPast = this.isDateWithinFiveDays(dueDate) && chequeType != 'Collected' && chequeType != 'Withdrawn' && chequeType != 'Replaced';
      return {
        ...item,
        isCollectedInPast
      };
    });
    this.deposites = updatedData;
  }
  isDateWithinFiveDays(inputDate: any): boolean {
    const currentDate = new Date();
    const futureDate = new Date(inputDate);
    futureDate.setDate(futureDate.getDate() + 5);
    let value = currentDate >= futureDate;
    return value;
  }

  onSortChange(sort: any) {
    if (sort?.direction && sort?.column) {
      switch (sort.column) {
        case 'chequeNo':
          this.tableConfig.filter.Sort = sort.direction === 'desc' ? 3 : 2;
          break;
        case 'customerName':
          this.tableConfig.filter.Sort = sort.direction === 'desc' ? 5 : 4;
          break;
        case 'bank':
          this.tableConfig.filter.Sort = sort.direction === 'desc' ? 7 : 6;
          break;
        case 'chequeDate':
          this.tableConfig.filter.Sort = sort.direction === 'desc' ? 9 : 8;
          break;
        case 'chequeCollectionJod':
          this.tableConfig.filter.Sort = sort.direction === 'desc' ? 11 : 10;
          break;
        case 'collectedAt':
          this.tableConfig.filter.Sort = sort.direction === 'desc' ? 13 : 12;
          break;
        case 'status':
          this.tableConfig.filter.Sort = sort.direction === 'desc' ? 15 : 14;
          break;
        default:
          break;
      }
    } else {
      this.tableConfig.filter.Sort = 1;
    }
    this.fetchData();
  }

  onPageChange(page: number) {
    this.page = page;
    this.fetchData();
  }

  initTableColumns() {
    this.tableColumns = [
      {
        key: 'select',
        label: 'Select All',
        canSort: true,
      },
      {
        key: 'chequeNo',
        label: 'Cheque No',
        canSort: true,
      },
      {
        key: 'customer.customerId',
        label: 'Customer Account',
      },
      {
        key: 'customerName',
        label: 'Customer Name',
        canSort: true,
      },
      {
        key: 'bank',
        label: 'Cheque Bank',
        canSort: true,
      },
      {
        key: 'chequeDate',
        label: 'Due Date',
        canSort: true,
      },
      {
        key: 'collectedAmount',
        label: 'Amount',
        canSort: true,
      },
      {
        key: 'currency',
        label: 'Currency',
      },
      {
        key: 'chequeCollectionJod',
        label: 'Amount (JOD)',
      },
      {
        key: 'collectedAt',
        label: 'Collection Date',
        canSort: true,
      },
      {
        key: 'markaziaBA',
        label: 'Markazia Bank Account',
      },
      {
        key: 'status',
        label: 'Status',
        canSort: true,
      },
      {
        key: 'action',
        label: 'Action',
      },
    ];
  }

  getStatusLabel(item: any): string {
    if (item && item.name && item.name.length > 0) {
      return item.name[0].lookupName;
    }
    return '';
  }
  onSelectData(selectedData: any) {

  }
  selecteddata: any[] = [];
  getSelectedItem(data: any, event: any): any {
    if (this.selecteddata.length == 0 && event.target.checked) {
      this.sort = 1;
      this.page = 1;
      if (this.tableConfig.filter.Status != data.status) {
        this.selecteddata.push(data);
        this.tableConfig.filter.Status = data.status;
        this.fetchData();
      } else
        this.selecteddata.push(data);
    }
    else if (event.target.checked) {
      this.selecteddata.push(data);
    } else {
      const index = this.selecteddata.findIndex(selectedDataItem => {
        return this.areObjectsEqual(selectedDataItem, data);
      });
      if (index !== -1) {
        this.selecteddata.splice(index, 1);
      }
    }
  }
  areObjectsEqual(obj1: any, obj2: any) {
    return JSON.stringify(obj1) === JSON.stringify(obj2);
  }
  multiSelect() {
    this.showMultiSelect = !this.showMultiSelect;
    if (!this.showMultiSelect) {
      delete this.tableConfig.filter.Status;
      this.selecteddata = [];
      this.fetchData()
    }
  }
  removeSearch() {
    this.searchText.setValue(null);
    this.sort = 1;
    this.selecteddata = [];
    this.page = 1;
    delete this.tableConfig.filter.Customer;
    this.fetchData();
  }
  removeChequeSearch() {
    this.searchChequeText.setValue(null);
    this.sort = 1;
    this.page = 1;
    this.selecteddata = [];
    delete this.tableConfig.filter.ChequeNo;
    this.fetchData();
  }

  handleCategoryChange(event: any) {
    this.tableConfig.filter.Status = event.id;
    // this.statusId = event?.id;
    this.sort = 1;
    this.page = 1;
    this.selecteddata = [];
    this.fetchData();
  }
  onClear() {
    delete this.tableConfig.filter.Status;
    // this.statusId = null;
    this.selecteddata = [];
    this.page = 1;
    this.fetchData();
  }

  /**
 * filter selection
 */
  bank: boolean = false;
  amount: boolean = false;
  displayAmount = new FormControl(0);
  collectionDate: boolean = false;
  dueDate: boolean = false;
  markaziaBank: boolean = false;
  markaziaBankList = [];
  filterbank(event: any) {
    this.bank = event.checked;
  }
  filteramount(event: any) {
    this.amount = event.checked;
  }
  filterMarkaziaBank(event: any) {
    this.markaziaBank = event.checked;
    //let params = `?pageSize=1000&status=1001`;
    //this.depositservice.getSageBanksAccounts(params).subscribe(response => {
    //  this.markaziaBankList = response.data;
    //})
    this.creditcardservice.getBankAccounts(`pageSize=1000&status=2001`).subscribe(response => {
      this.markaziaBankList = response.data;
    })
  }
  filterCollectionDates(event: any) {
    this.collectionDate = event.checked;
  }
  filterDueDates(event: any) {
    this.dueDate = event.checked;
  }
  inputvalue(value: any) {
    let checkPoint = false;
    const inputValue = value?.target?.value;
    if (inputValue && inputValue.includes('.')) {
      const parts = inputValue.split('.');
      if (parts.length === 2 && parts[1].length > 0) {
        if (inputValue === '0.0')
          checkPoint = true;
        else if (inputValue === '0.00')
          checkPoint = true;
        else checkPoint = false;
      } else if (parts.length === 2 && parts[1].length === 0) {
        checkPoint = true;
      } else {
        checkPoint = false;
      }
    }
    if (!checkPoint && parseFloat(inputValue) > 0) {
      this.tableConfig.filter.Amount = parseFloat(inputValue).toFixed(3);
      this.displayAmount.patchValue(parseFloat(parseFloat(inputValue).toFixed(3)));
      this.page = 1;
      this.selecteddata = [];
      this.sort = 1;
      this.fetchData();
    }
    else if (inputValue.includes('-')) {
      this.displayAmount.patchValue(0);
    }
    else if (isNaN(parseFloat(inputValue))) {
      this.displayAmount.patchValue(0);
    }
    else if (parseFloat(inputValue) == 0 && !checkPoint) {
      this.displayAmount.patchValue(0);
    }
  }
  /**
* get Branches
*/

  banksList: any;
  getbanks() {
    const api1$ = this.depositservice.getBankLookups(11);
    const api2$ = this.depositservice.getLookups('', `lookupTypeId=28&pageSize=1000`);
    const api3$ = this.depositservice.getLookups('', `lookupTypeId=29&pageSize=1000`);
    forkJoin([api1$, api2$, api3$]).subscribe(
      ([result1, result2, result3]) => {
        this.banksList = result1.data || []
        this.statusId = result2.data || [];
        this.actionList = result3.data || [];
      },
      (error) => {
        console.error(error);
      },
      () => {
        this.loading = false;
      }
    );
  }
  depositDetail(action: any, row: any) {
    let item = this.actionList.find(x => x.id == action);
    if (item?.id == 29002)
      this.openModalTrigger(item, row, 'Withdraw Cheque', true, true)
    else if (item?.id == 29001)
      this.openModalTrigger(item, row, 'Bounce Cheque', true, true)
    else if (item?.id == 29004)
      this.openModalTrigger(item, row, 'Re-deposited Cheque', true, true)
    else if (item?.id == 29006)
      this.openModalTrigger(item, row, 'Collect Cheque', true, true)
    else if (item?.id == ChequeActionEnum.PaidInCash)
      this.openModalTrigger(item, row, 'Pay In Cash', true, true)
    else if (item?.id == 29003)
      this.replace(item, row)
    else if (item?.id == ChequeActionEnum.Postpone)
      this.replace(item, row)
    else if (item?.name?.[0].lookupName == 'replace-view')
      this.replaceView()
  }
  openModalTrigger(actionType: any, detail: any, item: any, confirm: boolean, detailShow: boolean): void {
    debugger;
    let listArray: any[] = [];
    if (this.showMultiSelect) {
      this.selecteddata.forEach(element => {
        const data = this.deposites.find((a: any) => a.id === element.id);
        listArray.push(data);
      });
    }
    else
      listArray = [detail];
    const modalRef = this.modalService.open(DepositChequeModalComponent, {
      backdrop: 'static',
      keyboard: false,
      size: 'xl',
    });
    modalRef.componentInstance.details = listArray;
    modalRef.componentInstance.title = item;
    modalRef.componentInstance.isConfirmShow = confirm;
    modalRef.componentInstance.detailShow = detailShow;
    modalRef.componentInstance.actionType = actionType;
    modalRef.componentInstance.sendtoLoadData.subscribe((result: any) => {
      if (result) {
        if (!this.showMultiSelect) {
          this.fetchData();
        }
        else
          this.multiSelect();

      }
      modalRef.dismiss();
      //this.modalService.dismissAll();
      // this.getList();
    });
  }
  replace(actionType: any, detail: any) {
    let listArray: any[] = [];
    if (this.showMultiSelect) {
      this.selecteddata.forEach(element => {
        const data = this.deposites.find((a: any) => a.id === element.id);
        listArray.push(data);
      });
    }
    else
      listArray = [detail];
    let component = (actionType.id == 29003) && ReplaceChequeComponent || PostponeChequeComponent;
    const modalRef = this.modalService.open(component, { backdrop: 'static', keyboard: false, size: 'xl', windowClass: 'custom-modal-popup' });
    modalRef.componentInstance.details = listArray;
    modalRef.componentInstance.actionType = actionType;
    modalRef.componentInstance.sendtoLoadData.subscribe((result: any) => {
      if (result) {
        /*this.ngOnInit();*/
        this.multiSelect();

      }
      modalRef.dismiss();
    });
  }

  replaceView() {
    const modalRef = this.modalService.open(ReplacedChequeDetailViewComponent, {
      backdrop: 'static',
      keyboard: false,
      size: 'xl',
    });
    modalRef.componentInstance.sendtoLoadData.subscribe((result: any) => {
      console.log('resendtoLoadDatasult', result);
      this.modalService.dismissAll();
      // this.getList();
    });
  }

  newDropdownbank: any = [];
  removebankFilter() {
    this.newDropdownbank = [];
    this.dropDownbankId = [];
    delete this.tableConfig.filter.BankId;
    this.bank = false;
    this.page = 1;
    this.selecteddata = [];
    this.sort = 1;
    this.fetchData();
  }


  removeCollectionDateFilter() {
    this.collectionDate = false;
    delete this.tableConfig.filter.FromDate;
    delete this.tableConfig.filter.ToDate;
    this.selecteddata = [];
    this.page = 1;
    this.sort = 1;
    this.fetchData();
  }

  removeDueCollectionDateFilter() {
    this.dueDate = false;
    delete this.tableConfig.filter.FromDueDate;
    delete this.tableConfig.filter.ToDueDate;
    this.selecteddata = [];
    this.page = 1;
    this.sort = 1;
    this.fetchData();
  }
  removeAmount() {
    this.displayAmount.patchValue(0);
    this.amount = false;
    delete this.tableConfig.filter.Amount;
    this.page = 1;
    this.selecteddata = [];
    this.sort = 1;
    this.fetchData();
  }
  dropDownbankId: any[] = [];
  applybank(item: any, event: any) {
    const obj4 = item.name?.[0].lookupName;
    const bankId = item.id;
    if (event.target.checked) {
      // checking if the checkbox has been checked

      this.newDropdownbank.push(obj4); // pushing object to newArray[]
      this.dropDownbankId.push(bankId); // pushing object to newArray[]
    } else {
      this.newDropdownbank = this.newDropdownbank.filter(
        (v: any) => v !== obj4
      ); // if the checkbox has been unchecked removing the object from the array

      this.dropDownbankId = this.dropDownbankId.filter(
        (x) => x !== bankId
      );
    }
    this.tableConfig.filter.BankId = this.dropDownbankId;
    this.selecteddata = [];
    this.page = 1;
    this.fetchData();
  }
  handleMarkaziaBankChange(event: any) {
    this.tableConfig.filter.accountId = event?.map((x: any) => x.accountId).toString() || '';
    this.selecteddata = [];
    this.page = 1;
    this.sort = 1;
    this.fetchData();
  }
  removeMarkaziaBank() {
    this.markaziaModel = [];
    delete this.tableConfig.filter.accountId;
    this.markaziaBank = false;
    this.page = 1;
    this.sort = 1;
    this.selecteddata = [];
    this.fetchData();
  }
  onDateValueChange(event: any) {
    var pipe = new DatePipe('en-US');
    let startDate = pipe.transform(event[0]) || '';
    let endDate = pipe.transform(event[1]) || '';
    this.tableConfig.filter.FromDate = startDate;
    this.tableConfig.filter.ToDate = endDate;
    this.selecteddata = [];
    this.page = 1;
    this.sort = 1;
    this.fetchData();
  }

  onDueDateValueChange(event: any) {
    var pipe = new DatePipe('en-US');
    let startDate = pipe.transform(event[0]) || '';
    let endDate = pipe.transform(event[1]) || '';
    if (startDate) {
      const fromDate = new Date(startDate);
      fromDate.setDate(fromDate.getDate() + 1); // Adding one day
      const formattedFromDate = fromDate.toISOString();
      this.tableConfig.filter.FromDueDate = formattedFromDate;

    } else
      this.tableConfig.filter.FromDueDate = startDate;
    if (endDate) {
      const fromDate = new Date(endDate);
      fromDate.setDate(fromDate.getDate() + 1); // Adding one day
      const formattedFromDate = fromDate.toISOString();
      this.tableConfig.filter.ToDueDate = formattedFromDate;

    } else
      this.tableConfig.filter.ToDueDate = endDate;
    this.selecteddata = [];
    this.page = 1;
    this.sort = 1;
    this.fetchData();
  }

  IsPDCFilter(IsPDC: any) {
    this.tableConfig.filter.IsPDC = IsPDC;
    this.selecteddata = [];
    this.page = 1;
    this.sort = 1;
    this.fetchData();
  }


  canPerformAction(catId: number, subCatId: number, perItemName: number) {
    return this.permission.checkPermission(catId, subCatId, perItemName);
  }
  multi() {
    this.showMultiSelect = false
    this.selecteddata = [];
    this.page = 1;
    this.sort = 1;
    delete this.tableConfig.filter.Status;
    this.fetchData();
  }
}
