import { Component, ElementRef, EventEmitter, Input, Output, QueryList, ViewChildren } from '@angular/core';
import { FormArray, FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { ToastrService } from 'ngx-toastr';
import { ModalMessageComponent } from 'src/app/shared/modals/modal-message/modal-message.component';
import { DepositService } from 'src/app/shared/services/deposit.service';
import { MessageModalComponent } from '../../shared/modals/message-modal/message-modal.component';
import { ChequeActionEnum } from '../../shared/enums/enum';
import { DatePipe } from '@angular/common';

@Component({
  selector: 'app-deposit-cheque-modal',
  templateUrl: './deposit-cheque-modal.component.html',
  styleUrls: ['./deposit-cheque-modal.component.scss']
})
export class DepositChequeModalComponent {
  @Input() details;
  @Input() actionType: any;
  @Input() title = '';
  @Input() isConfirmShow = false;
  @Input() detailShow = false;
  @Output() sendtoLoadData = new EventEmitter();
  message: string = '';
  messageError: string = '';
  isLoading: boolean = false;
  reasonForm !: FormGroup;
  saveSubmitted: boolean = false;
  reasonList = [];
  filesToUpload: any[] = [];
  acceptedFileTypes: string = 'image/*,application/pdf';
  chequeActionEnum = ChequeActionEnum
  actionList = [
    { name: 'Pay In Cash', id: ChequeActionEnum.PaidInCash },
    { name: 'Replace', id: ChequeActionEnum.Replace },
  ]
  @ViewChildren('fileRef') fileRefs: QueryList<ElementRef> = new QueryList<ElementRef>();
  actionTypeId = 0;
  bankConfirmationDate = new FormControl(new Date());
  constructor(private formBuilder: FormBuilder, private deposiService: DepositService,
    private modalService: NgbModal,
    private toastrService: ToastrService,
    private _datePipe: DatePipe,
  ) { }
  ngOnInit() {
    console.log(this.details);
    this.initForm();
  }
  getActionReasons() {
    let params = `?actionId=${this.actionType.id}&pageSize=1000`;
    this.deposiService.getActionReasons(params).subscribe(response => {
      this.reasonList = response.data;
    })
  }
  initForm() {
    this.reasonForm = this.formBuilder.group({
      chequeArray: this.formBuilder.array([this.initFormGroup()])
    });
    this.getActionReasons();
    this.actionTypeId = this.actionType?.id;
    if (this.actionType.id != ChequeActionEnum.Redeposit && this.actionType.id != ChequeActionEnum.Collect) {
      this.formArray.clear();
      this.details.forEach((x, index) => {
        this.formArray.push(this.initFormGroup());
        if (this.actionType.id != ChequeActionEnum.Bounce) {
          this.setValidator('attachment', Validators.required, index);
        }
        if (this.actionType.id != ChequeActionEnum.PaidInCash) {
          this.setValidator('reasonId', Validators.required, index);
        }
        if (this.actionType.id == ChequeActionEnum.PaidInCash) {
          this.setValidator('receiptVoucherNumber', Validators.required, index);
        }
        if (this.actionType.id == ChequeActionEnum.Withdraw) {
          this.setValidator('nextAllowedAction', Validators.required, index);
        }
      })
    }
  }
  initFormGroup() {
    return this.formBuilder.group({
      receiptVoucherNumber: [''],
      chequeCollectionDate: [new Date()],
      note: [''],
      attachment: [null],
      reasonId: [null],
      nextAllowedAction: [null],
    });
  }
  get formArray() {
    return this.reasonForm.get('chequeArray') as FormArray;
  }
  setValidator(controlName: string, validator: any, index: number) {
    const control = this.formArray.at(index).get(controlName);
    if (control && validator) {
      control.setValidators(validator);
      control.updateValueAndValidity();
    }
  }
  dismissModal() {
    this.modalService.dismissAll();
  }
  close() {
    this.sendtoLoadData.emit('true');
  }
  onFileSelected(event: any, index: number): void {
    let files = [...event.target.files];
    files.forEach((file: File) => {
      this.deposiService.fileToBase64(file).then(response => {
        this.uploadFile(file, index)
        this.filesToUpload[index] = response; //Adding item on Specific Index of Array because oninit array is empty and always adding item to 0 index if we slect index 2; 
      })
    })
  }
  removeFileHandle(event: any, index: number) {
    this.filesToUpload.splice(index, 1);
    this.formArray.at(index).get('attachment')?.setValue('');
    this.fileRefs.toArray()[index].nativeElement.value = null;
  }
  uploadFile(file: any, index: number) {
    const formData = new FormData();
    formData.append('Folder', 'ChequeImages');
    formData.append('File', file);
    this.deposiService.uploadFile(formData).subscribe(response => {
      if (response.isSuccess) {
        this.formArray.at(index).get('attachment')?.setValue(response['data']);
      }
    })
  }
  get f() {
    return this.reasonForm;
  }
  saveForm() {
    if (this.isConfirmShow && this.f.valid) {
      let cheques: any[] = [];
      if (this.details.length > 1) {
        let formArray = this.formArray.value;
        this.details.forEach((x, index) => {
          let receiptNo = formArray[index]?.receiptVoucherNumber || '';
          let collectionDate = this._datePipe.transform(formArray[index]?.chequeCollectionDate, 'yyyy-MM-dd') || '';
          let obj = {
            depositedChequeId: x.id,
            actionFile: formArray[index]?.attachment,
            reasonId: formArray[index]?.reasonId,
            notes: formArray[index]?.note,
            ...(receiptNo ? { receiptVoucherNumber: receiptNo } : {}),
            ...(collectionDate ? { chequeCollectionDate: collectionDate } : {}),
          }
          cheques.push(obj);
        })
        let obj = { actionId: this.actionType.id, cheques }
        this.submitData(obj);
      }
      else {
        this.details.forEach((element, index) => {
          this.isLoading = true;
          let formData = new FormData();
          formData.append('BankConfirmationDate', this._datePipe.transform(this.bankConfirmationDate.value, 'yyyy-MM-dd') || '');
          formData.append('DepositedChequeId', element?.id.toString());
          formData.append('ActionId', this.actionType.id.toString());
          formData.append('Notes', this.formArray.value[index]?.note);
          formData.append('ActionFile', this.filesToUpload[0]?.file || '');
          formData.append('ReasonId', this.formArray.value[index]?.reasonId || '');
          if (this.actionTypeId == ChequeActionEnum.PaidInCash) {
            formData.append('ReceiptVoucherNumber', this.formArray.value[index]?.receiptVoucherNumber || '');
          }
          if (this.actionTypeId == ChequeActionEnum.Bounce) {
            formData.append('chequeCollectionDate', this._datePipe.transform(this.formArray.value[index]?.chequeCollectionDate, 'yyyy-MM-dd') || '');
          }
          if (this.actionTypeId == ChequeActionEnum.Withdraw) {
            formData.append('nextAllowedAction', this.formArray.value[index]?.nextAllowedAction || '');
          }
          this.submitData(formData);
        });
      }
    }
    else {
      this.f.markAllAsTouched();
      return;
    }
  }
  submitData(formData: any) {
    let api;
    if (this.details.length == 1) {
      api = this.deposiService.actionOnCheques(formData);
    }
    else {
      api = this.deposiService.actionOnChequesMulti(formData);
    }
    api.subscribe({
      next: response => {
        if (response.isSuccess) {
          this.responseModal('success', `Your ${(this.actionType?.name?.[0].lookupName).toLowerCase()} action successfully performed`);
          this.sendtoLoadData.emit(true);
        }
      },
      error: (err) => {
        this.responseModal('error', err?.error?.errors[0].errorMessageEn);
      },
    })
      .add(() => (this.isLoading = false));
  }
  responseModal(type: any, message: any) {
    const ref = this.modalService.open(MessageModalComponent);
    ref.componentInstance.type = type;
    ref.componentInstance.message = message;
  }
}

