import { Observable } from "rxjs";
import { ApiResponse } from "../models";
import { environment } from "src/environments/environment";
import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { toFilteringUrl } from "../utility";
import urlJoin from "url-join";
import { AuthService } from "./auth.service";


@Injectable({
  providedIn: 'root',
})

export class DepositService {
  constructor(private http: HttpClient, private authService: AuthService) { }
  getDepositCheques(filter: any): Observable<ApiResponse> {

    const headers = {
      Authorization: 'Bearer ' + localStorage.getItem("token"),
    };
    return this.http.get<ApiResponse>(
      toFilteringUrl(urlJoin(environment.apiUrl2, '/Cheques/GetDepositedCheques'), filter),
      { headers }
    );
  }
  getPermissions(id: any): Observable<ApiResponse> {

    const headers = {
      Authorization: 'Bearer ' + localStorage.getItem("token"),
    };
    return this.http.get<ApiResponse>(environment.apiUrl2 + '/User/GetUserPermissions?userId=' + this.authService.userId + '&portalId=' + id,
      { headers }
    );
  }
  getLookups(id?: any, params?: string): Observable<ApiResponse> {

    const headers = {
      Authorization: 'Bearer ' + localStorage.getItem("token"),
    };
    let query = '';
    if (params)
      query = `/Lookups/GetLookups?${params}`;
    else
      query = `/Lookups/GetLookups?lookupTypeId=${id}`
    return this.http.get<ApiResponse>(environment.apiUrl2 + query,
      { headers }
    );
  }
  getActionReasons(params: string): Observable<ApiResponse> {
    const headers = {
      Authorization: 'Bearer ' + localStorage.getItem("token"),
    };
    return this.http.get<ApiResponse>(environment.apiUrl + `/ChequesActionsManagement/GetReasons${params}`,
      { headers }
    );
  }
  getDueCheques(params: string): Observable<ApiResponse> {
    const headers = {
      Authorization: 'Bearer ' + localStorage.getItem("token"),
    };
    return this.http.get<ApiResponse>(environment.apiUrl2 + `/Cheques/GetDueCheques${params}`,
      { headers }
    );
  }
  getSageBanksAccounts(params: string): Observable<ApiResponse> {
    const headers = {
      Authorization: 'Bearer ' + localStorage.getItem("token"),
    };
    return this.http.get<ApiResponse>(environment.apiUrl + `/Mainfund/GetSageBanksAccounts${params}`,
      { headers }
    );
  }
  getBankLookups(id: any): Observable<ApiResponse> {

    const headers = {
      Authorization: 'Bearer ' + localStorage.getItem("token"),
    };
    return this.http.get<ApiResponse>(environment.apiUrl2 + '/Lookups/GetLookups?lookupTypeId=' + id + "&PageNo=0&PageSize=1000",
      { headers }
    );
  }
  getChequesActionsLog(id: any): Observable<ApiResponse> {

    const headers = {
      Authorization: 'Bearer ' + localStorage.getItem("token"),
    };
    return this.http.get<ApiResponse>(environment.apiUrl2 + '/Cheques/GetChequesActionsLog?chequeId=' + id,
      { headers }
    );
  }
  getChequeDetails(id: any): Observable<ApiResponse> {
    const headers = {
      Authorization: 'Bearer ' + localStorage.getItem("token"),
    };
    return this.http.get<ApiResponse>(environment.apiUrl2 + '/Cheques/GetChequeDetails?ChequeId=' + id,
      { headers }
    );
  }
  markDelivered(data: any): Observable<ApiResponse> {
    const headers = {
      Authorization: 'Bearer ' + localStorage.getItem("token"),
    };
    return this.http.post<ApiResponse>(urlJoin(environment.apiUrl2, '/Cheques/MarkDelivered'), data, { headers });
  }
  updateCheque(data: any): Observable<ApiResponse> {
    const headers = {
      Authorization: 'Bearer ' + localStorage.getItem("token"),
    };
    return this.http.post<ApiResponse>(urlJoin(environment.apiUrl2, '/Cheques/UpdateCheque'), data, { headers });
  }
  confirmChequeAction(data: any): Observable<ApiResponse> {
    const headers = {
      Authorization: 'Bearer ' + localStorage.getItem("token"),
    };
    return this.http.post<ApiResponse>(urlJoin(environment.apiUrl2, '/Cheques/ConfirmChequeAction'), data, { headers });
  }
  rejectChequeAction(data: any): Observable<ApiResponse> {
    const headers = {
      Authorization: 'Bearer ' + localStorage.getItem("token"),
    };
    return this.http.post<ApiResponse>(urlJoin(environment.apiUrl2, '/Cheques/RejectChequeAction'), data, { headers });
  }
  actionOnCheques(data: any): Observable<ApiResponse> {
    const headers = {
      Authorization: 'Bearer ' + localStorage.getItem("token"),
    };
    return this.http.post<ApiResponse>(urlJoin(environment.apiUrl2, '/Cheques/ActionsOnCheques'), data, { headers });
  }
  actionOnChequesMulti(data: any): Observable<ApiResponse> {
    const headers = {
      Authorization: 'Bearer ' + localStorage.getItem("token"),
    };
    return this.http.post<ApiResponse>(urlJoin(environment.apiUrl2, '/Cheques/ActionsOnChequesMulti'), data, { headers });
  }
  uploadFile(data: any): Observable<ApiResponse> {
    const headers = {
      Authorization: 'Bearer ' + localStorage.getItem("token"),
    };
    return this.http.post<ApiResponse>(urlJoin(environment.apiUrl2, '/Upload/UploadFile'), data, { headers });
  }
  uploadChequeImage(data: any): Observable<ApiResponse> {
    const headers = {
      Authorization: 'Bearer ' + localStorage.getItem("token"),
    };
    return this.http.post<ApiResponse>(urlJoin(environment.apiUrl2, '/Cheques/UploadChequeImage'), data, { headers });
  }
  getChequeActionDetails(ChequeId: any, ActionId: any): Observable<ApiResponse> {
    const headers = {
      Authorization: 'Bearer ' + localStorage.getItem("token"),
    };
    return this.http.get<ApiResponse>(urlJoin(environment.apiUrl2, '/Cheques/GetChequeActionDetails?ChequeId=' + ChequeId + "&ActionId=" + ActionId), { headers });
  }
  GetDashboardCard(): Observable<ApiResponse> {
    const headers = {
      Authorization: 'Bearer ' + localStorage.getItem("token"),
    };
    return this.http.get<ApiResponse>(urlJoin(environment.apiUrl2, '/Dashboard/GetDashboardCards'), { headers });
  }
  GetChequesAgingList(filetr: any): Observable<ApiResponse> {
    const headers = {
      Authorization: 'Bearer ' + localStorage.getItem("token"),
    };
    return this.http.get<ApiResponse>(
      toFilteringUrl(urlJoin(environment.apiUrl2, '/Dashboard/GetChequesAgingList'), filetr), { headers });
  }
  GetChequesAgingGraph(filetr: any): Observable<ApiResponse> {
    const headers = {
      Authorization: 'Bearer ' + localStorage.getItem("token"),
    };
    return this.http.get<ApiResponse>(
      toFilteringUrl(urlJoin(environment.apiUrl2, '/Dashboard/GetChequesAgingTotals'), filetr), { headers });
  }
  fileToBase64 = async (file: any) =>
    new Promise((resolve, reject) => {
      const reader = new FileReader()
      reader.readAsDataURL(file)
      reader.onload = () => {
        if (file.type !== 'application/pdf') {
          resolve({ fileType: file.type, base64: reader.result, file, fileName: file.name })
        }
        else {
          resolve({ fileType: file.type, file, fileName: file.name })
        }
      }
      reader.onerror = (e) => reject(e)
    })
}
